import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { effectProxy } from '@devsnc/sn-controls-common';
import { some } from 'lodash';

const FETCH_DATA_START = 'QUERY_BASE_FETCH_DATA_START';
const FETCH_DATA_SUCCESS = 'QUERY_BASE_FETCH_DATA_SUCCESS';
const FETCH_DATA_FAILURE = 'QUERY_BASE_FETCH_DATA_FAILURE';

export const FETCH_DATA = 'QUERY_BASE_FETCH_DATA';

export const queryBaseFetchDataEffectHandler = (query, variableList) =>
	createGraphQLEffect(query, {
		variableList,
		headerList: ['x-request-cancelable'],
		startActionType: FETCH_DATA_START,
		successActionType: FETCH_DATA_SUCCESS,
		errorActionType: FETCH_DATA_FAILURE
	});

export default function(query, variableList) {
	return {
		[FETCH_DATA]: effectProxy(
			queryBaseFetchDataEffectHandler(query, variableList)
		),

		[FETCH_DATA_START]: ({ updateProperties }) => {
			updateProperties({
				isSearching: true
			});
		},

		[FETCH_DATA_SUCCESS]: ({ updateProperties, action: { payload } }) => {
			const { errors, data } = payload;
			if (errors && errors.length !== 0) {
				// Only reset searching state when error is _not_ a transaction cancelation
				if (!some(errors, e => e.errorType === 'ExecutionAborted')) {
					updateProperties({
						results: [],
						isSearching: false
					});
				}

				return;
			}

			updateProperties({
				results: data,
				isSearching: false
			});
		},

		[FETCH_DATA_FAILURE]: ({ updateProperties }) => {
			updateProperties({
				results: [],
				isSearching: false
			});
		}
	};
}
