// 3rd party imports
import React from 'react';
import PropTypes from 'prop-types';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import {
	isFunction,
	isObject,
	toString,
	debounce,
	isEmpty,
	reject,
	includes,
	map,
	find
} from 'lodash';

// Function imports
import {
	fieldPropTypes,
	toPills,
	toValues,
	isEmail,
	withValueChangeCallbacks
} from '@devsnc/sn-controls-common';

// Component imports
import NowTypeahead from '@servicenow/now-record-typeahead';
import NowTypeaheadMultiple from '@servicenow/now-record-typeahead-multiple';

import {
	getDeclarativeActionsProps,
	getSerializedChanges,
	getEncodedRecord
} from '@devsnc/sn-controls-common';

import { FETCH_DATA } from './queryBaseActionHandlers';

/**
 * Common component to manage inputs that fetch suggestions from a GraphQL
 * datasource. Handles single-select values (aka reference fields), and
 * multi-select values (like a watch list).
 */
export class GlideQueryBase extends React.PureComponent {
	static componentName = 'sn-glide-query-base';
	/**
	 * constructor
	 * @param {object} props
	 */
	constructor(props) {
		super(props);

		let {
			displayValue,
			searchDebounce,
			value,
			multiSelection,
			disableDisplayValueWarning
		} = props;

		this.assertValidProps(props);

		this.runReadQuery = debounce(this.runReadQuery, searchDebounce);

		/**
		 * local state of the input for highlighting and user interaction
		 * @type {object}
		 * @param {bool} isFocused toggle if the element is in focused state
		 * @param {bool} isActive toggle if the options are active
		 * @param {bool} isSearching toggle the search text if we are executing a search query
		 * @param {string} inputVal the value the user is typing in
		 * @param {array} options list of avalable options
		 * @param {array} recents list of recent options
		 * @param {array} selectedSearchResult list of selected option indexes
		 */

		let inputVal = '';
		let pills = [];
		let displayWarning;

		if (multiSelection) {
			if (value) {
				pills = toPills(value.split(','), displayValue);
				if (!disableDisplayValueWarning)
					displayWarning = this.getPillWarning(pills);
			}
		} else {
			if (value) {
				if (displayValue) {
					inputVal = toString(displayValue);
				} else {
					inputVal = `(${t('empty')})`;

					if (!disableDisplayValueWarning) {
						displayWarning = this.getMessage(
							'warning',
							t(
								'Record {0} does not exist or does not have a display value set.',
								value
							)
						);
					}
				}
			} else {
				inputVal = '';
			}
		}
		this.state = {
			displayWarning,
			isSearching: false,
			inputVal,
			pills,
			options: [],
			recents: []
		};
	}

	assertValidProps(props) {
		const { dispatch, mapResults, getVariables } = props;

		if (!dispatch) {
			throw new Error('Required prop `dispatch` has not been provided');
		}

		if (!isFunction(mapResults)) {
			throw new Error(
				`Required prop 'mapResults' is not a function. Got ${typeof mapResults}`
			);
		}

		if (!isFunction(getVariables)) {
			throw new Error(
				`Required prop 'getVariables' is not a function. Got ${typeof getVariables}`
			);
		}
	}

	toMessageArray = messages =>
		Array.isArray(messages) ? messages : messages ? [messages] : [];

	getMessage = (type, message) => {
		return {
			type,
			message
		};
	};

	getPillWarning = pills => {
		const { disableDisplayValueWarning } = this.props;
		const badPills = pills.filter(pill => !pill.displayValue);
		let warning = null;
		if (!disableDisplayValueWarning) {
			if (badPills.length > 1) {
				warning = this.getMessage(
					'warning',
					t(
						'Records {0} do not exist or do not have a display value set.',
						badPills.map(pill => pill.value).join(', ')
					)
				);
			} else if (badPills.length === 1) {
				warning = this.getMessage(
					'warning',
					t(
						'Record {0} does not exist or does not have a display value set.',
						badPills[0].value
					)
				);
			}
		}
		return warning;
	};

	/**
	 * callback function to intercept onChange events from the controlled input, reflect the data back as a value and
	 * query for new options if needed.
	 * @param {string} value input value
	 */
	onInputValueChange = event => {
		const inputVal = event.target.value;
		const { multiSelection } = this.props;
		if (isEmpty(inputVal) && !multiSelection) {
			this.onSelectOption({ displayValue: '' });
		}
		this.runReadQuery(inputVal);
		this.setState({
			inputVal,
			isSearching: !isEmpty(inputVal)
		});
	};

	/**
	 * function pushes a new value and displayValue
	 * @param {string} value
	 * @param {string} displayValue
	 */

	onSelectOption = ({ value, displayValue }) => {
		this.setState(
			{
				inputVal: toString(displayValue),
				options: []
			},
			() => this.props.onValueChange({ value, displayValue })
		);
	};

	/**
	 * function that takes an array of pill objects and updates value and displayValue
	 * @param {array} newPills array of pill objects
	 */
	addPills = ({ value: allPills }) => {
		this.emitPillChange(allPills);
		this.setState({ inputVal: '', isActive: false, options: [] });
	};

	emitPillChange(pills) {
		const { values, displayValues } = toValues(pills);

		this.props.onValueChange({
			value: values.join(','),
			displayValue: displayValues
		});
	}

	/**
	 * onBlur callback that resets the input value on blur
	 */
	onInputBlur = () => {
		let {
			value,
			displayValue,
			multiSelection,
			disableDisplayValueWarning,
			results
		} = this.props;
		let { inputVal, displayWarning } = this.state;

		if (!multiSelection) {
			displayWarning = null;

			if (inputVal.length > 0) {
				const { options, recents, totalCount } = this.mapResults(results);
				/*
				 * DEF0087979: when there is only one option, choose it on blur
				 */
				if (totalCount === 1) {
					const onlyOption = recents[0] || options[0];
					this.onSelectOption(onlyOption);
				} else {
					if (!value) {
						// if user tabs on selected option, the blur occurs before receiving props and
						// value is still null. This finds the value from the options
						let foundOption = find(
							options || recents,
							({ displayValue }) => displayValue === inputVal
						);
						if (foundOption) {
							value = foundOption.value;
							displayValue = foundOption.displayValue;
						}
					}
					if (value) {
						if (displayValue) {
							inputVal = toString(displayValue);
						} else {
							inputVal = `(${t('empty')})`;
							if (!disableDisplayValueWarning) {
								displayWarning = this.getMessage(
									'warning',
									t(
										'Record {0} does not exist or does not have a display value set.',
										value
									)
								);
							}
						}
					} else {
						let error = this.getMessage(
							'error',
							t(
								'Please select a value from the list of results in the dropdown.'
							)
						);
						this.props.onValueChange({
							value,
							displayValue: inputVal,
							error
						});
					}
				}
			}
		}

		this.setState({
			inputVal,
			options: [],
			displayWarning
		});
	};

	/**
	 * special onEnter callback that refreshes options list
	 */
	onEnter = () => {
		const { inputVal } = this.state;
		this.setState({
			isSearching: true
		});

		this.runReadQuery(inputVal);
	};

	onClick = () => {
		if (this.props.searchOnClick) {
			this.onEnter();
		}
	};

	/**
	 * resource callback function
	 * @param {string} chars string to pass into the query
	 */
	runReadQuery(inputVal) {
		const variables = this.props.getVariables(inputVal);
		if (isObject(variables)) {
			this.props.dispatch(
				FETCH_DATA,
				{
					serializedChanges: getSerializedChanges(this.props),
					encodedRecord: getEncodedRecord(this.props.formData),
					...variables
				},
				{ stopPropagation: true }
			);
		}
	}

	mapResults(data) {
		const attributes =
			this.props.dictionary && this.props.dictionary.attributes;
		const { options, recents, totalCount } = data
			? this.props.mapResults(
					data,
					attributes && isObject(find(attributes, { name: 'ref_ac_order_by' }))
			  )
			: {};
		const selectedValues = map(this.state.pills, 'value');

		const removeSelected = collection =>
			reject(
				collection,
				item => isObject(item) && includes(selectedValues, item.value)
			);

		return {
			options: removeSelected(options),
			recents: removeSelected(recents),
			totalCount
		};
	}

	/**
	 * triggered when element gets new props and we decide whether or not to update the value
	 * @param {object} newProps
	 */
	componentWillReceiveProps(newProps) {
		const {
			value,
			displayValue,
			multiSelection,
			disableDisplayValueWarning,
			results,
			isSearching,
			liveUpdate
		} = newProps.properties || newProps;
		let { inputVal, pills, displayWarning } = this.state;

		this.assertValidProps(newProps);

		let { options, recents, totalCount } = this.mapResults(results);

		if (
			this.props.value !== value ||
			this.props.displayValue !== displayValue
		) {
			if (multiSelection) {
				if (
					liveUpdate &&
					!liveUpdate.isUserModified &&
					(!this.props.liveUpdate ||
						(this.props.liveUpdate &&
							this.props.liveUpdate.value !== liveUpdate.value))
				) {
					pills = liveUpdate.value
						? toPills(liveUpdate.value.split(','), liveUpdate.displayValue)
						: [];
					inputVal = liveUpdate.value;
				} else {
					pills = value ? toPills(value.split(','), displayValue) : [];
				}
				displayWarning = this.getPillWarning(pills);
			} else {
				displayWarning = null;
				if (value) {
					if (displayValue) {
						inputVal = toString(displayValue);
					} else {
						inputVal = `(${t('empty')})`;
						if (!disableDisplayValueWarning) {
							displayWarning = this.getMessage(
								'warning',
								t(
									'Record {0} does not exist or does not have a display value set.',
									value
								)
							);
						}
					}
				} else {
					inputVal = toString(displayValue);
				}
			}
		}

		this.setState({
			displayWarning,
			inputVal,
			isSearching,
			pills,
			options,
			recents,
			totalCount
		});
	}

	getReference = () => {
		const {
			componentId,
			name,
			dispatch,
			label,
			description,
			messages,
			required,
			reversed,
			invalid,
			formData,
			innerRef,
			searchUsingStartsWith,
			searchOnClick,
			maxSearchMatches,
			includeHeaders
		} = this.props;
		const {
			inputVal,
			isSearching,
			options,
			recents,
			totalCount,
			displayWarning
		} = this.state;

		const dAProps = getDeclarativeActionsProps(
			this.props.declarativeActionsProps,
			this.props
		);

		const readonly = isAttrTrue(this.props.readonly);
		const placeholder = readonly ? '—' : this.props.placeholder;

		return (
			<now-record-typeahead
				componentId={`${componentId}_referencePicker`}
				name={name}
				dispatch={dispatch}
				placeholder={placeholder}
				label={label}
				value={inputVal}
				selectedItem={this.props.value}
				messages={[
					...this.toMessageArray(messages),
					...this.toMessageArray(displayWarning)
				]}
				description={description}
				readonly={readonly}
				required={required}
				reversed={reversed}
				invalid={invalid}
				loading={isSearching}
				options={options}
				recents={recents}
				totalMatchesCount={totalCount}
				onChange={this.onInputValueChange}
				onValueChange={this.onSelectOption}
				onBlur={this.onInputBlur}
				onClick={this.onClick}
				onEnter={this.onEnter}
				innerRef={innerRef}
				searchUsingStartsWith={searchUsingStartsWith}
				searchOnClick={searchOnClick}
				includeHeaders={includeHeaders}
				maxSearchMatches={maxSearchMatches}
				dAProps={dAProps}
				formData={formData}
				dispatch={dispatch}
			/>
		);
	};

	getReferenceMultiple = () => {
		const {
			componentId,
			name,
			dispatch,
			placeholder,
			label,
			description,
			messages,
			readonly,
			required,
			reversed,
			invalid,
			searchUsingStartsWith,
			maxSearchMatches
		} = this.props;
		const {
			inputVal,
			isSearching,
			options,
			recents,
			totalCount,
			pills,
			displayWarning
		} = this.state;
		return (
			<now-record-typeahead-multiple
				componentId={`${componentId}_referencePickerMultiple`}
				name={name}
				placeholder={placeholder}
				label={label}
				value={inputVal}
				pills={pills}
				dispatch={dispatch}
				messages={[
					...this.toMessageArray(messages),
					...this.toMessageArray(displayWarning)
				]}
				description={description}
				readonly={readonly}
				required={required}
				reversed={reversed}
				invalid={invalid}
				loading={isSearching}
				options={options}
				recents={recents}
				totalMatchesCount={totalCount}
				onChange={this.onInputValueChange}
				onValueChange={this.addPills}
				onBlur={this.onInputBlur}
				onClick={this.onClick}
				onEnter={this.onEnter}
				searchUsingStartsWith={searchUsingStartsWith}
				maxSearchMatches={maxSearchMatches}
			/>
		);
	};

	/**
	 * render
	 * @return {ReactElement} markup
	 */
	render() {
		const { multiSelection } = this.props;
		if (multiSelection) return this.getReferenceMultiple();
		return this.getReference();
	}
}

GlideQueryBase.propTypes = {
	...fieldPropTypes,

	/**
	 * Dictionary values and attributes for the field
	 */
	dictionary: PropTypes.object,

	/**
	 * results from a graphQL request
	 */
	results: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),

	/**
	 * Callbacks for Forms consumption
	 * @type {function}
	 */
	onValueChange: PropTypes.func,

	/**
	 * Function to get variables for datasource read.
	 *
	 * @param {String} input - The current value of the input
	 * @return {Object?} variables - Variables passed to the datasource read
	 * call. If this returns null/undefined, the read call will not be triggered.
	 */
	getVariables: PropTypes.func.isRequired,

	/**
	 * Maps the datasource result into arrays of options and recents, to be
	 * displayed as choices in the dropdown.
	 *
	 * @param {Object} data - GraphQL result data
	 * @return {Object} An object with the following shape:
	 *	 @property {Array} recents
	 *	 @property {Array} options
	 *
	 * Both `recents` and `options` should have the following shape:
	 * @property {any} value - The underlying value for the item
	 * @property {String} displayValue - User-friendly display for the item
	 */
	mapResults: PropTypes.func.isRequired,

	/**
	 * Integer to set the debounce interval for hitting the api when searching reference fields
	 * Defaults to 400ms
	 */
	searchDebounce: PropTypes.number,

	/**
	 * Value can be anything
	 */
	value: PropTypes.any,

	/**
	 * When true, prevent the user from typing in free-form email addresses.
	 * Instead, an email must be selected from the list of available options.
	 */
	disableEmailFreeformEntry: PropTypes.bool,

	/**
	 * displayValue can be string or number or array of values
	 */
	displayValue: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.number,
		PropTypes.array
	]),

	/**
	 * Flag that switches between multiple and single value reference inputs
	 */
	multiSelection: PropTypes.bool,

	/**
	 * Please use `disableEmailFreeformEntry` instead
	 */
	noEmail: PropTypes.bool,

	/**
	 * Callback to show record
	 */
	referenceAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * Callback to launch reference list modal
	 */
	searchAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * Flag to hide the reference icon
	 */
	hideReferenceIcon: PropTypes.bool,

	/**
	 * Flag to show the reference icon in readonly mode
	 */
	referenceAddonReadonly: PropTypes.bool,

	/**
	 * Flag to toggle turning off display value warning
	 */
	disableDisplayValueWarning: PropTypes.bool,

	/**
	 * formData object containing glide form data
	 */
	formData: PropTypes.object,

	/**
	 * Declarative Action props
	 */
	declarativeActionsProps: PropTypes.object,

	/**
	 * LiveUpdate info
	 */
	liveUpdate: PropTypes.object,

	/**
	 * Flag to indicate the results are loading
	 */
	isSearching: PropTypes.bool,

	/**
	 * Flag to Enable "contains" searches for reference autocompleters
	 */

	searchUsingStartsWith: PropTypes.bool,

	/**
	 * Flag to searching when clicking on field
	 */
	searchOnClick: PropTypes.bool,

	/**
	 * Flag to including the header of dropdown
	 */
	includeHeaders: PropTypes.bool
};

GlideQueryBase.style = NowTypeahead.style + NowTypeaheadMultiple.style;

GlideQueryBase.defaultProps = {
	multiSelection: false,
	searchDebounce: 400,
	disableReferenceQualifier: false,
	disableEmailFreeformEntry: false,
	ignoreReferenceQualifier: false,
	referenceAddon: true,
	searchAddon: true,
	hideReferenceIcon: false,
	referenceAddonReadonly: false,
	noEmail: false,
	disableDisplayValueWarning: false,
	isSearching: false,
	searchOnClick: true
};

export default withValueChangeCallbacks(
	{ stopBubbling: ['NOW_RECORD_TYPEAHEAD', 'NOW_RECORD_TYPEAHEAD_MULTIPLE'] },
	GlideQueryBase
);
